@import url(https://fonts.googleapis.com/css?family=Raleway:100,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
  background: var(--color-primary);
  height: 100vh;
  width: 100vw;
}

.App {
  height: 100vh;
  margin: auto;
  text-align: center;
}

div#root{
  width: 100%;
  height: 100%;
}

.windows-bar {
  background-color: #ddd;
  border-bottom: 2px solid #dadada;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  padding: 7px;
  font-size: 14pt;
  color: #6f6f6f;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#info-tabs {
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.12);
}

.windows-bar .header{
  flex: 1 1;
  margin-left: 24px;
}

.windows.histogram {
  display: grid;
  justify-content: center;
  justify-items: center;
}

.histogram-options {
  display: flex;
  justify-content: space-around;
  justify-self: normal;
}

.column {
  display: flex;
}

.empty-histogram {
    height: 180px;
    background: #f3f3f3;
    margin: 2px;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
}

/*
* Grid specification
*/

.grid-container {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: repeat(20,1fr);
    grid-template-columns: 73% auto;
    grid-gap: 10px;
    padding:  0 10px;
}

.grid-item {
    height: 100%;
}

.item-menu {
    z-index: 1;
    grid-column-start: 1;
    grid-column-end: 3;
}

.item-workspace {
    grid-row: 2 / span 19;
}

.item-info {
    grid-row: 2 / span 12;
}

.item-action {
    grid-row: 14 / span 7;
}

.item-footer {
    grid-column: 1 / span 2;
}

.title {
    font-family: 'Raleway', sans-serif;
    text-transform: uppercase;
    padding-right: 10px;
    margin-right: 10px;
    border-right: 1px solid #babdbe;
}

.title a {
    color: black;
    text-decoration: none;
}

.title .second {
    font-weight: 700;
}
.footer a {
    text-decoration: none;
}
